body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.QueryModeSelect > div {
  border: none !important;
}

.QueryModeSelect.ant-select-open > div {
  box-shadow: 0 0 0 0 transparent;
}

.QueryBar {
  min-width: 520px;
  height: 34px;
  display: flex;
  align-items: flex-start;
  background-color: white;
}

.QueryBar-container {
  background-color: white;
  border-radius: 2px;
  display: flex;
  justify-content: flex-start;
  width: 100%;
  border: 1px solid #e8e8e8;
}

.ResultCard {
  overflow: auto;
}

em {
  background-color: #ffe58f;
}

.ClassifierResultContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
}

.ClassifierResultContainer-body {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 16px;
  width: 100%;
}

.ClassifierResultContainer-progressbar-text {
  font-size: 12px;
}

.ClassifierResultContainer-progressbar {
  box-sizing: border-box;
  border-radius: 12px;
  height: 8px;
  width: 100%;
  margin-bottom: 8px;
}

.ClassifierResultContainer-progressbar > div {
  border-radius: 12px;
}

.ClassificationCard {
  overflow: auto;
}

.QueryHistoryCard {
  overflow: auto;
}

.QueryListItem {
  padding: 8px 4px;
  border-bottom: 1px solid #e8e8e8;
  cursor: pointer;
}

.QueryListItem:hover {
  background: #e6f7ff;
}

.QueryListItem:last-child {
  margin-bottom: 24px;
}

.QueryListItem-footer {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.45);
}

.CollectionSelect-select {
  min-width: 280px;
  margin-right: 8px !important;
}


.DocumentDetailModal-body {
  display: grid;
  grid-template-columns: 1fr 400px;
  grid-gap: 16px;
  overflow-x: auto;
  height: 70vh;
}

@media only screen and (max-width: 1200px) {
  .App-name,
  .App-logo {
    display: none;
  }
}

.DocumentDetailModal-body-content {
  overflow: scroll;
}

.DocumentDetailModal-body-field table {
  table-layout: fixed;
}

.BasicRow:hover {
  background: #e6f7ff;
  cursor: pointer;
}

.BasicRow-avatar {
  background-color: #f56a00 !important;
  vertical-align: middle !important;
}

.WordCloudRow {
  display: grid;
  grid-column-gap: 16px;
  grid-template-columns: 2fr 1fr;
  border-bottom: 1px solid #e8e8e8;
  padding: 8px 0px;
}

.WordCloudRow > .BasicRow {
  border-bottom: transparent !important;
}

.WordCloudRow > .BasicRow:hover {
  background: transparent;
}

.App-header {
  width: 100%;
  padding: 0 32px !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1000;
}

.App-header-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.App-name-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 0px 16px 0px 0px;
}

.App-name-title {
  color: white;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
}

.App-name-subtitle {
  font-size: 10px;
  color: white;
  line-height: 10px;
}

.App-body {
  height: calc(100vh - 64px);
  max-height: calc(100vh - 64px);
}

.App-body-content {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: auto;
  grid-column-gap: 16px;
  height: calc(100vh - 64px);
  padding: 16px;
}

@media only screen and (max-width: 1200px) {
  .App-name-container,
  .App-logo {
    display: none;
  }
}

.App-logo {
  margin-right: 8px;
}

.App-body-content-column {
  display: grid;
  grid-row-gap: 16px;
  height: calc(100vh - 96px);
}

